import React from "react"
import {
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  Media,
  Badge
} from "reactstrap"
import PerfectScrollbar from "react-perfect-scrollbar"
import * as Icon from "react-feather"
import classnames from "classnames"
import Autocomplete from "../../../components/@vuexy/autoComplete/AutoCompleteComponent"
import { history } from "../../../history"
import WorkflowApprovalDetailsModal from "../../../views/pages/Setups/WorkflowApprovalDetails/Modals/WorkflowApprovalDetailsModal";
import * as API from "../../../utility/crud/app.crud"
import * as Date from "../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'


const UserDropdown = props => {
  return (
    <DropdownMenu right>
      <DropdownItem tag="a" href="#">
        <Icon.User size={14} className="mr-50" />
        <span className="align-middle">Edit Profile</span>
      </DropdownItem>
      <DropdownItem tag="a" href="#">
        <Icon.Mail size={14} className="mr-50" />
        <span className="align-middle">My Inbox</span>
      </DropdownItem>
      <DropdownItem tag="a" href="#">
        <Icon.CheckSquare size={14} className="mr-50" />
        <span className="align-middle">Tasks</span>
      </DropdownItem>
      <DropdownItem tag="a" href="#">
        <Icon.MessageSquare size={14} className="mr-50" />
        <span className="align-middle">Chats</span>
      </DropdownItem>
      <DropdownItem tag="a" href="#">
        <Icon.Heart size={14} className="mr-50" />
        <span className="align-middle">WishList</span>
      </DropdownItem>
      <DropdownItem divider />
      <DropdownItem
        tag="a"
        href="#"
        onClick={e => history.push("/auth/logout")}
      >
        <Icon.Power size={14} className="mr-50" />
        <span className="align-middle">Log Out</span>
      </DropdownItem>
    </DropdownMenu>
  )
}


const Notification = props => {

  const { title, body, createdOn } = props;

  const IconTag = Icon[props.icon ? props.icon : "Bell"];

  const approveNotification = () => {
    return props.handleApproveNotification(props)
  }

  return (
    <div className="d-flex justify-content-between" onClick={approveNotification}>
      <Media className="d-flex align-items-start">
        <Media left href="#">
          <IconTag size={21} className="font-medium-5 primary" />
        </Media>
        <Media body>
          <Media heading className="primary media-heading" tag="h6">
            {title}
          </Media>
          <p className="notification-text">
            {body}
          </p>
        </Media>
        <small>
          <time
            className="media-meta"
            dateTime="2015-06-11T18:29:20+08:00"
          >
            {createdOn}
          </time>
        </small>
      </Media>
    </div>
  )
}

class NavbarUser extends React.PureComponent {
  state = {
    navbarSearch: false,
    suggestions: [],
    show: false,

    loggedInUser: this.props.user,

    workFlowDetails: [],

    WORKFLOW_DETAILS_ID: 0,
    WORKFLOW_SETUPS_ID: 0,
    APPROVAL_DESCRIPTION: '',
    APPROVAL_COMMENT: '',
    APPROVED_BY: '',
    APPROVAL_STATUS: '',
    APPROVAL_TYPE: '',

    CREATED_BY: this.props.user.EMAIL,
    MODIFIED_BY: this.props.user.EMAIL,
    CREATED_DATE: Date.ShowDate(),
    MODIFIED_DATE: Date.ShowDate(),
    InsertUpdate: '',
  }

  componentDidMount() {
    this.populateData()
    this.updateSearchSuggestions()
  }

  handleNavbarSearch = () => {
    this.setState({
      navbarSearch: !this.state.navbarSearch
    })
  }

  populateData() {
    const getdata = `          
      query {
        WorkFlowDetails {
          WORKFLOW_DETAILS_ID
          WORKFLOW_SETUPS_ID
          WORKFLOW_SETUP {
            WORKFLOW_SETUPS_ID
            APPROVAL_SEQUENCE
            APPROVAL_LEVEL
            GROUP_USERS
            WORKFLOW {
                MODULE_TYPE {
                    MODULE_ID
                    MODULE_NAME
                }
            }
          }
          APPROVAL_DESCRIPTION
          APPROVAL_COMMENT
          APPROVED_BY
          USER {
            USER_ID
            USER_NAME
            EMAIL
          }
          APPROVAL_STATUS
          CREATED_BY
          MODIFIED_BY
          CREATED_DATE
          MODIFIED_DATE
        }
      }
    `;

    API.getData(getdata)
      .then(({ data }) => {

        let workFlowDetails = []

        data.data.WorkFlowDetails.forEach((workflowDetail, index) => {
          if (workflowDetail.APPROVAL_STATUS === "PENDING" && workflowDetail.WORKFLOW_SETUP.GROUP_USERS && workflowDetail.WORKFLOW_SETUP.GROUP_USERS.includes(this.state.loggedInUser.USER_ID)) {
            workFlowDetails.push(workflowDetail)
          }
        });

        this.setState({ 
          workFlowDetails: workFlowDetails
        })

      }).catch(error => {
        if (error.response || error.request) {
          toastr.error('Error Loading Data', 'Failed to Load Workflow Details Data');
        }
      })

  }

  updateSearchSuggestions = () => {

    let data = [];

    if (this.props.menuConfig.length) {

      this.props.menuConfig.forEach(primaryItem => {
        if (!primaryItem.children) {
          data.push({ ...primaryItem, "link": primaryItem.navLink, "icon": primaryItem.icon })
        } else {

          primaryItem.children.forEach(secondaryItem => {
            if (!secondaryItem.children) {
              data.push({ ...secondaryItem, "link": secondaryItem.navLink, "icon": primaryItem.icon })
            } else {

              secondaryItem.children.forEach(childItem => {
                if (!childItem.children) {
                  data.push({ ...childItem, "link": childItem.navLink, "icon": primaryItem.icon })
                }
                // else {
                //   console.log(childItem)
                // }
              })

            }
          })

        }
      })

    }

    this.setState({ suggestions: [{ data: data }] })
  }

  clearData() {
    this.setState({
      WORKFLOW_DETAILS_ID: '',
      WORKFLOW_SETUPS_ID: '',
      APPROVAL_DESCRIPTION: '',
      APPROVAL_COMMENT: '',
      APPROVED_BY: '',
      APPROVAL_STATUS: '',
      validated: false
    })
  }

  handleShow = () => {
    this.setState({
      show: true,
      InsertUpdate: 'Update'
    });
  }

  handleHide = () => {
    this.setState({
      show: false
    });

    this.clearData();
  }

  handleApproveNotification = (e) => {

    if(e.type === "APPROVAL_DETAIL") {

      this.state.workFlowDetails.forEach(detail => {
        if (detail.WORKFLOW_DETAILS_ID === e.typeId) {
          this.setState({
            WORKFLOW_DETAILS_ID: detail.WORKFLOW_DETAILS_ID,
            WORKFLOW_SETUPS_ID: detail.WORKFLOW_SETUPS_ID,
            APPROVAL_DESCRIPTION: detail.APPROVAL_DESCRIPTION,
            APPROVAL_COMMENT: detail.APPROVAL_COMMENT,
            APPROVED_BY: detail.APPROVED_BY,
            APPROVAL_STATUS: detail.APPROVAL_STATUS,
            APPROVAL_TYPE: detail.WORKFLOW_SETUP.WORKFLOW.MODULE_TYPE.MODULE_NAME,
            show: true,
            InsertUpdate: 'Update'
          })
        }
      })

    } else {
      toastr.info('SYSTEM INFO', 'Not Approval detail to be approved');
    }

  }

  handleSubmit = (event) => {
    event.preventDefault();
    const validate = event.currentTarget;
    if (validate.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }
    else {

      const formEdit = `
            mutation{
                updateWorkFlowDetails(
                    WORKFLOW_DETAILS_ID: ${this.state.WORKFLOW_DETAILS_ID},
                    WORKFLOW_SETUPS_ID: ${this.state.WORKFLOW_SETUPS_ID},
                    APPROVAL_DESCRIPTION: "${this.state.APPROVAL_DESCRIPTION}",
                    APPROVAL_COMMENT: "${this.state.APPROVAL_COMMENT}",
                    APPROVED_BY: ${this.props.user.USER_ID},
                    APPROVAL_STATUS: "${this.state.APPROVAL_STATUS ? this.state.APPROVAL_STATUS : 'PENDING'}",
                    MODIFIED_BY: "${this.state.MODIFIED_BY}",              
                    MODIFIED_DATE: "${this.state.MODIFIED_DATE}"
                ){
                    WORKFLOW_DETAILS_ID
                }
            }
        `;

      if (this.state.InsertUpdate !== 'Insert') {
        API.update(formEdit)
          .then(() => {
            this.clearData();
            this.handleHide();
            this.populateData();
            toastr.info('Update Record', 'Record Updated Successfully');
          }).catch(error => {
            if (error.response || error.request) {
              toastr.error('Error Loading Data', 'Failed to Load Data');
            }
          })
      }
    }

    this.setState({
      validated: true
    })
  }

  render() {
    return (
      <ul className="nav navbar-nav navbar-nav-user float-right">

        <NavItem className="nav-search" onClick={this.handleNavbarSearch}>
          <NavLink className="nav-link-search">
            <Icon.Search size={21} data-tour="search" />
          </NavLink>
          <div
            className={classnames("search-input", {
              open: this.state.navbarSearch,
              "d-none": this.state.navbarSearch === false
            })}
          >
            <div className="search-input-icon">
              <Icon.Search size={17} className="primary" />
            </div>
            <Autocomplete
              className="form-control"
              suggestions={this.state.suggestions}
              filterKey="title"
              filterHeaderKey="groupTitle"
              grouped={true}
              placeholder="Search..."
              autoFocus={true}
              clearInput={this.state.navbarSearch}
              externalClick={e => {
                this.setState({ navbarSearch: false })
              }}
              onKeyDown={e => {
                if (e.keyCode === 27 || e.keyCode === 13) {
                  this.setState({
                    navbarSearch: false
                  })
                  this.props.handleAppOverlay("")
                }
              }}
              customRender={(
                item,
                i,
                filteredData,
                activeSuggestion,
                onSuggestionItemClick,
                onSuggestionItemHover
              ) => {
                const IconTag = Icon[item.icon ? item.icon : "X"]
                return (
                  <li
                    className={classnames("suggestion-item", {
                      active: filteredData.indexOf(item) === activeSuggestion
                    })}
                    key={i}
                    onClick={e => onSuggestionItemClick(item.link, e)}
                    onMouseEnter={() =>
                      onSuggestionItemHover(filteredData.indexOf(item))
                    }
                  >
                    <div
                      className={classnames({
                        "d-flex justify-content-between align-items-center":
                          item.file || item.img
                      })}
                    >
                      <div className="item-container d-flex">
                        {item.icon ? (
                          <IconTag size={17} />
                        ) : item.file ? (
                          <img
                            src={item.file}
                            height="36"
                            width="28"
                            alt={item.title}
                          />
                        ) : item.img ? (
                          <img
                            className="rounded-circle mt-25"
                            src={item.img}
                            height="28"
                            width="28"
                            alt={item.title}
                          />
                        ) : null}
                        <div className="item-info ml-1">
                          <p className="align-middle mb-0">{item.title}</p>
                          {item.by || item.email ? (
                            <small className="text-muted">
                              {item.by
                                ? item.by
                                : item.email
                                  ? item.email
                                  : null}
                            </small>
                          ) : null}
                        </div>
                      </div>
                      {item.size || item.date ? (
                        <div className="meta-container">
                          <small className="text-muted">
                            {item.size
                              ? item.size
                              : item.date
                                ? item.date
                                : null}
                          </small>
                        </div>
                      ) : null}
                    </div>
                  </li>
                )
              }}
              onSuggestionsShown={userInput => {
                if (this.state.navbarSearch) {
                  this.props.handleAppOverlay(userInput)
                }
              }}
            />
            <div className="search-input-close">
              <Icon.X
                size={24}
                onClick={(e) => {
                  e.stopPropagation()
                  this.setState({
                    navbarSearch: false
                  })
                  this.props.handleAppOverlay("")
                }}
              />
            </div>
          </div>
        </NavItem>


        <UncontrolledDropdown
          tag="li"
          className="dropdown-notification nav-item"
        >

          <DropdownToggle tag="a" className="nav-link nav-link-label" onClick={ () => this.props.handleForceParentRerender() }>
            <Icon.Bell size={21} />
            <Badge pill color="success" className="badge-up">
              {" "}
              {this.props.notifications.length}
              {" "}
            </Badge>
          </DropdownToggle>

          <DropdownMenu tag="ul" right className="dropdown-menu-media">

            <li className="dropdown-menu-header">
              <div className="dropdown-header mt-0">
                <h3 className="text-white">{this.props.notifications.length} New</h3>
                <span className="notification-title">App Notifications</span>
              </div>
            </li>

            <PerfectScrollbar
              className="media-list overflow-hidden position-relative"
              options={{
                wheelPropagation: false
              }}
            >

              {
                this.props.notifications.map(notification => {
                  return (
                    <Notification
                      {...notification}
                      handleApproveNotification={this.handleApproveNotification}
                    />
                  )
                })
              }

            </PerfectScrollbar>

            <li className="dropdown-menu-footer">
              <DropdownItem tag="a" className="p-1 text-center">
                <span className="align-middle">Read all notifications</span>
              </DropdownItem>
            </li>
          </DropdownMenu>

          <WorkflowApprovalDetailsModal
            validated={this.state.validated}
            show={this.state.show}
            handleHide={this.handleHide}
            InsertUpdate={this.state.InsertUpdate}
            handleSubmit={this.handleSubmit}
            handleShowModuleTypeSearch={this.handleShowModuleTypeSearch}
            handleShowStatusSearch={this.handleShowStatusSearch}

            ApprovalDescription={this.state.APPROVAL_DESCRIPTION}
            ApprovalType={this.state.APPROVAL_TYPE}

            ApprovalComment={this.state.APPROVAL_COMMENT}
            handleChangeApprovalComment={e => this.setState({ APPROVAL_COMMENT: e.target.value })}

            ApprovalStatus={this.state.APPROVAL_STATUS}
            handleChangeApprovalStatus={e => this.setState({ APPROVAL_STATUS: e.target.value })}

            showdelete={this.state.showdelete}
            handleHideDelete={this.handleHideDelete}
            handleDelete={this.handleDelete}
          />

        </UncontrolledDropdown>


        <UncontrolledDropdown tag="li" className="dropdown-user nav-item">
          <DropdownToggle tag="a" className="nav-link dropdown-user-link">
            <div className="user-nav d-sm-flex d-none">
              <span className="user-name text-bold-600">
                {this.props.userName}
              </span>
              <span className="user-status">Available</span>
            </div>
            <span data-tour="user">
              <img
                src={this.props.userImg}
                className="round"
                height="40"
                width="40"
                alt="avatar"
              />
            </span>
          </DropdownToggle>
          <UserDropdown {...this.props} />
        </UncontrolledDropdown>
      </ul>
    )
  }
}

export default NavbarUser
