import React, { useState} from "react"
import ReactSync from "react-sync"
import { connect } from "react-redux"
import { Navbar } from "reactstrap"
import classnames from "classnames"
import { toastr } from 'react-redux-toastr'
import NavbarBookmarks from "./NavbarBookmarks" 
import NavbarUser from "./NavbarUser"
import userImg from "../../../assets/img/portrait/small/avatar-s-11.jpg"

import { BASE_URL } from '../../../utility/crud/app.crud';
import { store } from '../../../redux/storeConfig/store'


const ThemeNavbar = props => {
  const colorsArr = ["primary", "danger", "success", "info", "warning", "dark"]
  const navbarTypes = ["floating", "static", "sticky", "hidden"]

  const [, updateState] = React.useState();
  const forceUpdate = React.useCallback(() => updateState({}), []);

  const [notifications, setNotifications] = useState([])

  const { ...rest } = props

  const {
    auth: { token, user },
    menu: { menuConfig }
  } = store.getState()

  return (
    <ReactSync 
      { ...rest }
      url={BASE_URL}
      params={{
        query: `
          query {
            userNotifications(
              USER_ID: ${user.USER_ID}
            ) {
              NOTIFICATION_ID
              NOTIFICATION_ICON
              NOTIFICATION_TYPE
              NOTIFICATION_TYPE_ID
              NOTIFICATION_TITLE
              NOTIFICATION_BODY
              CREATED_DATE
            }
          }
        `
      }}
      headers={{
        Authorization: `Bearer ${token}`,
      }}
    >
      {
        ({ promise, data, error }) => {

          let incomingNotifications = []

          if (data !== null && data.data && data.data.userNotifications && data.data.userNotifications.length) {

            data.data.userNotifications.forEach(notification => {
              if (notification !== null) {
                incomingNotifications.push({
                  id: notification.NOTIFICATION_ID,
                  icon: notification.NOTIFICATION_ICON,
                  type: notification.NOTIFICATION_TYPE,
                  typeId: notification.NOTIFICATION_TYPE_ID,
                  title: notification.NOTIFICATION_TITLE,
                  body: notification.NOTIFICATION_BODY,
                  createdOn: notification.CREATED_DATE,
                })
              }
            });

          }

          if (incomingNotifications.length > 0) {
            
            if (notifications.length > 0) {
              
              const oldNotificationIds = notifications.map(notification => notification.id)

              const newNotifications = incomingNotifications.filter(incomingNotification => !oldNotificationIds.includes(incomingNotification.id))

              if (newNotifications.length > 0) {

                newNotifications.forEach(newNotification => {
                  toastr.info(
                    newNotification.title, 
                    newNotification.body,
                    {
                      position: "bottom-left"
                    }
                  )
                })

                setNotifications(incomingNotifications)

              }

            } else {
              setNotifications(incomingNotifications)
            }
            
          }
          
          return (
            <React.Fragment>
              <div className="content-overlay" />
              <div className="header-navbar-shadow" />
              <Navbar
                className={classnames(
                  "header-navbar navbar-expand-lg navbar navbar-with-menu navbar-shadow",
                  {
                    "navbar-light": props.navbarColor === "default" || !colorsArr.includes(props.navbarColor),
                    "navbar-dark": colorsArr.includes(props.navbarColor),
                    "bg-primary":
                      props.navbarColor === "primary" && props.navbarType !== "static",
                    "bg-danger":
                      props.navbarColor === "danger" && props.navbarType !== "static",
                    "bg-success":
                      props.navbarColor === "success" && props.navbarType !== "static",
                    "bg-info":
                      props.navbarColor === "info" && props.navbarType !== "static",
                    "bg-warning":
                      props.navbarColor === "warning" && props.navbarType !== "static",
                    "bg-dark":
                      props.navbarColor === "dark" && props.navbarType !== "static",
                    "d-none": props.navbarType === "hidden" && !props.horizontal,
                    "floating-nav":
                      (props.navbarType === "floating" && !props.horizontal) || (!navbarTypes.includes(props.navbarType) && !props.horizontal),
                    "navbar-static-top":
                      props.navbarType === "static" && !props.horizontal,
                    "fixed-top": props.navbarType === "sticky" || props.horizontal,
                    "scrolling": props.horizontal && props.scrolling

                  }
                )}
              >
                <div className="navbar-wrapper">
                  <div className="navbar-container content">
                    <div
                      className="navbar-collapse d-flex justify-content-between align-items-center"
                      id="navbar-mobile"
                    >
                      {props.horizontal ? (
                        <div className="logo d-flex align-items-center">
                          <div className="brand-logo mr-50"></div>
                          {/* <h2 className="text-primary brand-text mb-0">NCC</h2> */}
                        </div>
                      ) : null}
                      <div className="bookmark-wrapper">
                        <NavbarBookmarks
                          sidebarVisibility={props.sidebarVisibility}
                          handleAppOverlay={props.handleAppOverlay}
                        />
                      </div>
                      <NavbarUser
                        handleAppOverlay={props.handleAppOverlay}
                        changeCurrentLang={props.changeCurrentLang}
                        handleForceParentRerender={forceUpdate}
                        notifications={notifications}
                        user={props.user}
                        menuConfig={menuConfig}
                        userName={props.user.USER_NAME}
                        userImg={userImg}
                      />
                    </div>
                  </div>
                </div>
              </Navbar>
            </React.Fragment>
          )
        }
      }
    </ReactSync>
  )
}

const mapStateToProps = state => {
  return {
    user: state.auth.user
  }
}

export default connect(mapStateToProps)(ThemeNavbar)
