import React from 'react';
import { Button, Modal, Form } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";

class WorkflowApprovalDetailsModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} WorkFlow Approval Detail
                        </Modal.Title>
                    </Modal.Header>
                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>

                            <Form.Group controlId="Approval Type">
                                <Form.Label>Approval Type</Form.Label>
                                <Form.Control type="text" placeholder="No Approval Type"
                                    value={this.props.ApprovalType}
                                    onChange={this.props.changeApprovalType}
                                    name="Approval Type"
                                    required
                                    readOnly
                                />
                                <Form.Control.Feedback type="invalid">
                                    Please provide a valid description.
                                 </Form.Control.Feedback>
                            </Form.Group>

                            <Form.Group controlId="Approval Description">
                                <Form.Label>Approval Description</Form.Label>
                                <Form.Control type="text" as="textarea" rows="3" placeholder="Enter Approval Description"
                                    value={this.props.ApprovalDescription}
                                    onChange={this.props.changeApprovalDescription}
                                    name="Approval Description"
                                    required
                                    readOnly
                                />
                                <Form.Control.Feedback type="invalid">
                                    Please provide a valid description.
                                 </Form.Control.Feedback>
                            </Form.Group>

                            <Form.Group controlId="Approval Comment">
                                <Form.Label>Approval Comment</Form.Label>
                                <Form.Control type="text" as="textarea" rows="3" placeholder="Enter Approval Comment"
                                    value={this.props.ApprovalComment}
                                    onChange={this.props.handleChangeApprovalComment}
                                    name="Approval Comment"
                                    required
                                />
                                <Form.Control.Feedback type="invalid">
                                    Please provide a valid description.
                                 </Form.Control.Feedback>
                            </Form.Group>

                            <Form.Group controlId="ApprovalStatus">
                                <Form.Label>Approval Status</Form.Label>
                                <Form.Control as="select" required custom defaultValue={this.props.ApprovalStatus} onChange={this.props.handleChangeApprovalStatus} >                                    
                                    <option value="PENDING">Pending</option>
                                    <option value="APPROVED">Approved</option>
                                    <option value="REJECTED">Rejected</option>
                                </Form.Control>
                                <Form.Control.Feedback type="invalid">
                                    Please Select Approval Status
                                 </Form.Control.Feedback>

                            </Form.Group>

                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

            </div>
        );
    }
}

export default WorkflowApprovalDetailsModal;