

import { store } from '../../storeConfig/store'


const sortBookmarkSuggestions = () => {

  const {
    menu: { menuConfig }
  } = store.getState()

  let data = [];

  if (menuConfig.length) {

    menuConfig.forEach((primaryItem, index) => {
      if (!primaryItem.children) {        
        data.push({...primaryItem, link : primaryItem.navLink, icon: primaryItem.icon ? primaryItem.icon : 'Star', starred: false, target: primaryItem.title.toLowerCase().replace(/\s/g,'-').replace(/&/g,'and')})
      } else {

        primaryItem.children.forEach((secondaryItem, index) => {
          if (!secondaryItem.children) {
            data.push({...secondaryItem, link : secondaryItem.navLink, icon: primaryItem.icon ? primaryItem.icon : 'Star', starred: false, target: secondaryItem.title.toLowerCase().replace(/\s/g,'-').replace(/&/g,'and')})
          } else {
            
            secondaryItem.children.forEach(childItem => {
              if (!childItem.children) {
                data.push({...childItem, link : childItem.navLink, icon: primaryItem.icon ? primaryItem.icon : 'Star', starred: false, target: childItem.title.toLowerCase().replace(/\s/g,'-').replace(/&/g,'and')})
              } 
              // else {
              //   console.log(childItem)
              // }
            })

          }
        })

      }
    })

  }
  
  return data;
}

export default sortBookmarkSuggestions;